body {
  text-align: center;
}

input {
  height: 50px;
  background-color: ffffff;
  border: 1px solid #ccc;
  padding: 0 10px;
}

button {
  width: 100px;
  height: 50px;
  cursor: pointer;
  border: none;
  margin-bottom: 50px;
}

button:disabled {
  background-color: #e4e4e4;
}

.timer {
  justify-content: center;
  display: flex;
}

.field {
  width: 100px;
  border: 1px solid #ccc;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  padding: 10px 0;
  display: flex;
}

.field:last-child {
  margin-right: 0;
}

.value {
  font-size: 40px;
  font-weight: 700;
}

.label {
  color: #ccc;
}

